
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

export default {
name: "subStoreList",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
password: '',
isFocus: false,
isAdd: false,
isShadow: false,
isConfirm: false,
is_paypwd: true,
delId: null,
bankList: [],
totalPage: 0,
page: 1,
upLoading: false,
upLoadingComplete: false,
upmore: true,
};
},
created() {
this.getBankList();
},
mounted() {
this.$nextTick(function() {
isScrollBottom(this.scrollBottomCB);
});
},
methods: {
showConfirm(e){
const isTrue = this.isConfirm ? false :true
this.isConfirm=isTrue;
if(isTrue){
this.delId=e.target.dataset.id;
}else{
this.delId=null;
}
},
shadowToggle(){

if(this.is_paypwd) {
const isTrue = this.isAdd ? false :true;
this.isAdd=isTrue;
this.password='';
} else {
const isTrue = this.isShadow ? false :true;
this.isShadow=isTrue;
}

},
bindPwd(e){
var password=this.password;
if(password && password.length>6){
this.password=password.slice(0,6);
}else{
console.log(this.password)
console.log(this.password.length)
var length_password=this.password.length;
if(length_password==6){
this.checkPay();
}
}

},
async checkPay(){
let data={
plat: 3,
token:this.token,
account:this.account,
pwd: this.$encrypt(this.password)
};
const [err, res] = await api.checkPayPwd(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.shadowToggle();
this.$router.push('/bindNewCard');

}else{
this.$vux.toast.text(res.msg);
}


}

},
linkToPage(){

},
async delCard(){
let data={
plat: 3,
token:this.token,
account:this.account,
card_id: this.delId
};
const [err, res] = await api.delBindingBank(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}else{
if(res.code==2000){
this.$vux.toast.text('解除绑定成功');
this.delId=null;
this.isConfirm=false;
this.page=1;
this.getBankList();
}
}
},
async getBankList(){
let data={
plat: 3,
token:this.token,
account:this.account,
page:this.page
};
this.page++;
const [err, res] = await api.userBankList(data);
if(err){
this.$vux.toast.text(err.msg);
return;
}

this.bankList = this.bankList.concat(res.data.list);
this.is_paypwd=res.data.is_paypwd;
this.totalPage=res.data.page_size;

},

scrollBottomCB() {
this.getBankList();
}
},
filters: {},
computed: {
...mapGetters(["user", "account", "token"])
}
};



